.lp__achieve {
  .flickity-page-dots .dot {
    margin: 0 6px;
    background: $white;
    border: 2px solid $blue;
    opacity: 1;

    &.is-selected {
      background: $blue;
      border: unset;
    }
  }

  .flickity-prev-next-button {
    right: 0;
    width: 20px;
    height: 20px;
    background: url("../img/slide-arrow.svg") no-repeat center;
    box-shadow: none;

    &.previous {
      left: 0;
      transform: translateY(-50%) scaleX(-1);
    }
  }

  .flickity-button-icon {
    display: none;
  }
}
