.lp {
  $p: &;

  &__consultation {
    @include sp {
      background-color: $white2;
    }
  }

  &__image {
    &--presenting {
      max-height: getpc(319px);
      max-width: getpc(274px);
      height: auto;
      width: 100%;
      align-self: flex-end;
      transform: translateY(1px);
      @include desktop {
        max-height: 319px;
        max-width: 274px;
      }
    }
  }

  &__ellipsis {
    padding-left: getpc(58px);
    @include desktop {
      padding-left: 58px;
    }
    @include sp {
      padding: 0;
      margin: auto;
      transform: translateX(getpc(-10px));
    }
  }

  &__ellipsis #{$p}__dot {
    height: getpc(5px);
    width: getpc(5px);
    background-color: $orange;
    border-radius: 50%;
    display: inline-block;
    @include desktop {
      height: 5px;
      width: 5px;
    }
    @include sp {
      height: getsp375(7px);
      width: getsp375(7px);
    }
  }

  &__ellipsis #{$p}__dot:not(:first-child) {
    margin-left: getpc(21px);
    @include desktop {
      margin-left: 21px;
    }
    @include sp {
      margin-left: getsp375(25px);
    }
  }

  &__text {
    &--small {
      font-size: getpc(1.6rem);
      font-weight: $medium;
      line-height: getem(getpc(16), getpc(32));
      letter-spacing: getpc(.5px);
      @include desktop {
        font-size: 1.6rem;
        line-height: getem(16, 32);
        letter-spacing: .5px;
      }
      @include sp {
        font-size: getsp375(2.9rem);
        line-height: getsp375(6rem);
      }
    }

    &--underline {
      text-decoration: underline;
      color: $orange;
      text-underline-offset: 3px;
    }
  }

  &__flow-block {
    position: relative;
    height: getpc(800px);
    display: flex;
    flex-direction: column;
    gap: getpc(13px);
    counter-reset: step;
    @include desktop {
      height: 800px;
      gap: 13px;
    }
    @include sp {
      gap: getsp375(13px);
      height: max-content;
    }
  }

  &__flow-row {
    counter-increment: item;
    height: getpc(150px);
    display: flex;
    gap: getpc(10px);
    @include desktop {
      height: 150px;
      gap: 10px;
    }
    @include sp {
      min-height: getsp375(225px);
      height: getsp375(225px);
      gap: getsp375(10px);
    }
  }

  &__flow-row:first-child #{$p}__flow-column-title {
    border-radius: getpc(30px) 0 0 0;
    @include desktop {
      border-radius: 30px 0 0 0;
    }
    @include sp {
      border-radius: getsp375(30px) 0 0 0;
    }
  }

  &__flow-row:first-child #{$p}__flow-column-description {
    border-radius: 0 getpc(30px) 0 0;
    @include desktop {
      border-radius: 0 30px 0 0;
    }
    @include sp {
      border-radius: 0 getsp375(30px) 0 0;
    }
  }

  &__flow-row:last-child #{$p}__flow-column-title {
    background-color: $yellow2;
    border-radius: 0 0 0 getpc(30px);
    @include desktop {
      border-radius: 0 0 0 30px;
    }
    @include sp {
      border-radius: 0 0 0 getsp375(30px);
    }
  }

  &__flow-row:last-child #{$p}__flow-column-description {
    color: $orange;
    font-weight: $bold;
    border-radius: 0 0 getpc(30px) 0;
    @include desktop {
      border-radius: 0 0 30px 0;
    }
    @include sp {
      border-radius: 0 0 getsp375(30px) 0;
    }
  }

  &__flow #{$p}__section-wrap {
    @include desktop {
      padding: 100px 0 80px;
    }
    @include sp {
      padding: getsp375(50px) $sp-pad getsp375(60px) $sp-pad;
    }
  }

  &__flow-column-title {
    width: 34%;
    background-color: $yellow;
    display: flex;
    align-items: center;
    padding: 0 getpc(20px) 0 getpc(60px);
    position: relative;
    filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.05));
    @include desktop {
      padding: 0 20px 0 60px;
    }
    @include sp {
      padding: 0 getsp375(20px);
      flex-direction: column;
      filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.2));
    }
  }

  &__flow-block::before {
    position: absolute;
    content: '';
    border-left: getpc(5px) solid $yellow;
    height: 100%;
    margin-left: 17%; // 205/1200
    z-index: 0;
    @include desktop {
      border-left: 5px solid $yellow;
    }
    @include sp {
      margin-left: 16.5%; // 121/750
    }
  }

  &__flow-counter-step {
    height: getpc(60px);
    width: getpc(60px);
    min-width: getpc(60px);
    border-radius: getpc(10.45px);
    background-color: $white;
    position: relative;
    color: $blue;
    font-family: $novel;
    @include desktop {
      height: 60px;
      width: 60px;
      min-width: 60px;
      border-radius: 10.45px;
    }
    @include sp {
      margin-top: getsp375(40px);
      height: getsp375(60px);
      width: getsp375(60px);
      min-width: getsp375(60px);
      border-radius: getsp375(10.45px);
    }

    &::before {
      content: 'STEP';
      position: absolute;
      font-size: getpc(1.3rem);
      line-height: getpc(1.3rem);
      transform: translateX(50%);
      top: 5px;
      @include desktop {
        font-size: 1.3rem;
        line-height: 1.3rem;
      }
      @include sp {
        font-size: getsp375(1.3rem);
        line-height: (1.3rem);
        top: getsp375(0px);
      }
    }

    &::after {
      content: counter(item);
      position: absolute;
      font-size: getpc(3.5rem);
      line-height: getpc(3.5rem);
      transform: translateX(100%);
      top: getpc(20px);
      @include desktop {
        font-size: 3.5rem;
        line-height: 3.5rem;
      }
      @include sp {
        font-size: getsp375(3.5rem);
        line-height: getsp375(3.5rem);
        top: getsp375(20px);
      }
    }
  }

  &__flow-title {
    font-size: getpc(2.6rem);
    line-height: getpc(2.6rem);
    letter-spacing: getpc(1px);
    color: $white;
    font-weight: $bold;
    height: max-content;
    margin-left: getpc(24px);
    @include desktop {
      font-size: 2.6rem;
      line-height: 2.6rem;
      letter-spacing: 1px;
      margin-left: 24px;
    }
    @include sp {
      font-size: getsp375(2.8rem);
      line-height: getsp375(4rem);
      letter-spacing: getsp375(1px);
      margin: 0;
      margin-top: getsp375(20px);
      text-align: center;
    }
  }

  &__flow-column-description {
    background-color: $white;
    width: 66%;
    font-size: getpc(1.8rem);
    line-height: getpc(3.6rem);
    padding-left: getpc(60px);
    padding-right: getpc(40px);
    letter-spacing: getpc(2px);
    display: flex;
    align-items: center;
    font-weight: $medium;
    position: relative;
    filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.05));
    @include desktop {
      font-size: 1.8rem;
      line-height: 3.6rem;
      padding-left: 60px;
      padding-right: 40px;
      letter-spacing: 2px;
    }
    @include sp {
      padding: 0 getsp375(30px);
      font-size: getsp375(2.4rem);
      line-height: getsp375(4.5rem);
      letter-spacing: 0px;
      filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.2));
    }
  }

  &__operation-fee {
    th {
      width: 25%;
    }
  }

  &__operation-table-wrap {
    width: 90%;
    margin: 0 auto;
    @include sp {
      width: 100%;
    }
  }

  &__operation-table {
    margin: auto;
    height: max-content;
    width: 100%;
    max-width: getpc(1000px);
    text-align: center;
    font-weight: $bold;
    display: table;
    border-collapse: collapse;

    th,
    td {
      &:not(:last-child) {
        border-right: 3px solid $white;
        @include sp {
          border-right: 2px solid $white4;
        }
      }

      &:last-child {
        @include sp {
          border-right: 2px solid $white3;
        }
      }
    }
    
    th {
      height: getpc(36px);
      background-color: $yellow !important;
      color: $white;
      font-size: getpc(1.8rem);
      @include desktop {
        height: 36px;
        font-size: 1.8rem;
      }
      @include sp {
        font-size: getsp375(2.4rem);
      }
    }

    td {
      font-size: getpc(1.6rem);
      letter-spacing: 0.05em;
      background-color: $white4;
      @include desktop {
        font-size: 1.6rem;
      }
      @include sp {
        background-color: $white;
        font-size: getsp375(1.8rem);
        letter-spacing: 0;
      }

      &:first-child {
        background-color: $gray3;
        @include sp {
          border-bottom: 2px solid $white;
          border-right-color: $gray3;
        }
      }

      span {
        color: $orange;

        &.number {
          font-size: getpc(2.4rem);
          @include desktop {
            font-size: 2.4rem;
          }
          @include sp {
            font-size: getsp375(3.2rem);
          }
        }

        &.character {
          font-size: getpc(1.6rem);
          @include desktop {
            font-size: 1.6rem;
          }
          @include sp {
            font-size: getsp375(2rem);
          }
        }
      }
    }

    tr {
      width: getpc(247px);
      height: getpc(50px);
      @include desktop {
        width: 247px;
        height: 50px;
      }

      &:not(:last-child) {
        border-bottom: 3px solid $white;
        @include sp {
          border-bottom: 2px solid $white3;
        }
      }

      &:first-child {
        @include sp {
          border-bottom: none;
        }
      }

      &:last-child {
        @include sp {
          border-bottom: 2px solid $white3;
        }
      }
    }
  }

  &__operation-table--consult {
    tr {
      &:not(:last-child) {
        border-bottom-color: $white3;
      }
    }

    th,
    td {
      &:not(:last-child) {
        border-right-color: $white3;
      }
    }
  }

  &__service-contents {
    @include center;
    flex-wrap: wrap;
    gap: getpc(30px);
    @include desktop {
      gap: 30px;
    }
    @include sp {
      gap: getsp375(33px);
    }
  }

  &__service-content {
    filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.15));
    background-color: $white;
    position: relative;
    border-radius: getpc(30px);
    height: getpc(225px);
    width: getpc(175px);
    @include desktop {
      border-radius: 30px;
      height: 225px;
      width: 175px;
      padding: 46px 0 32px;
    }
    @include sp {
      height: getsp375(205px);
      width: getsp375(205px);
      padding: getsp375(20px) 0;
      gap: getsp375(18.45px);
    }

    &::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.15));
    }
  }

  &__service-content-icon {
    position: absolute;
    margin: 0 auto;
    left: 0;
    right: 0;
    bottom: getpc(100px);
    height: getpc(80px);
    width: auto;
    @include desktop {
      height: 80px;
    }
    @include sp {
      bottom: getsp375(91.72px);
      height: getsp375(80px);
    }
  }

  &__service-content-text {
    text-align: center;
    font-size: getpc(2rem);
    line-height: getem(20, 27);
    color: $blue;
    font-weight: $bold;
    height: 48px;
    position: absolute;
    margin: 0 auto;
    left: 0;
    right: 0;
    bottom: getpc(18px);
    @include center;
    @include desktop {
      font-size: 2rem;
    }
    @include sp {
      font-size: getsp375(2.5rem);
      line-height: getem(25, 30);
      bottom: getsp375(32px);
      font-size: getsp375(2.6rem);
      line-height: getsp375(3.2rem);
      max-height: getsp375(26px);
    }
  }

  &__consulting-fee {
    th {
      width: 33.33%;
    }

    tr {
      & :not(first-child) {
        background-color: $white;
      }
    }

    &-text {
      font-size: getpc(1.6rem);
      max-width: getpc(570px);
      width: 100%;
      line-height: getpc(3.2rem);
      font-weight: $medium;
      letter-spacing: 1.2px;
      @include desktop {
        font-size: 1.6rem;
        max-width: 570px;
        line-height: 3.2rem;
      }
    }

    ul {
      margin: getpc(30px) 0 getpc(40px);
      @include desktop {
        margin: 30px 0 40px;
      }
      @include sp {
        margin: getsp375(40px) 0 getsp375(56px);
      }
    }
  }

  &__faq {
    height: max-content;
    width: auto;
    background: url('../img/bg/1.jpg') no-repeat center center;
    background-size: cover;
  }

  &__faq #{$p}__section-wrap {
    padding: getpc(100px) 0;
    @include desktop {
      padding: 100px 0;
    }
    @include sp {
      padding: getsp375(77px) $sp-pad getsp375(60px);
    }
  }

  &__faq hr {
    background-color: $blue;
    margin: getpc(21.53px) 0;
    @include desktop {
      margin: 21.53px 0;
    }
    @include sp {
      margin: getsp375(18px) 0;
    }
  }

  &__qas {
    width: 90%;
    display: flex;
    gap: getpc(30px);
    flex-direction: column;
    @include desktop {
      gap: 30px;
    }
    @include sp {
      gap: getsp375(40px);
    }
  }

  &__qa {
    width: 100%;
    background-color: $white;
    border-radius: getpc(30px);
    padding: getpc(30px) getpc(49px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include desktop {
      border-radius: 30px;
      padding: 30px 49px;
    }
  }

  &__question,
  &__answer {
    display: flex;
    position: relative;
    align-items: flex-start;
    letter-spacing: 1px;

    &::after {
      position: absolute;
      left: 0;
      content: '';
      height: getpc(34px);
      width: getpc(34px);
      @include desktop {
        height: 34px;
      width: 34px;
      }
    }
  }

  &__question p,
  &__answer p {
    margin-left: getpc(20px);
    @include desktop {
      margin-left: 20px; 
    }
    @include sp {
      margin-left: getsp375(18.3px);
    }
  }

  &__question {
    position: relative;
    font-size: getpc(1.8rem);
    line-height: getpc(3.6rem);
    font-weight: $bold;
    @include desktop {
      font-size: 1.8rem;
      line-height: 3.6rem;
    }
  }

  &__question-icon {
    position: relative;
    height: getpc(34px);
    min-width: getpc(34px);
    background: url('../img/icons/q.png');
    background-size: getpc(34px) getpc(34px);
    @include desktop {
      min-width: 34px;
      height: 34px;
      background-size: 34px 34px;
    }

    &::after {
      position: absolute;
      content: 'Q';
      font-family: $novel;
      font-size: getpc(2rem);
      color: $white;
      @include center;
      height: 100%;
      width: 100%;
      @include desktop {
        font-size: 2rem;
      }
    }
  }

  &__answer {
    font-size: getpc(1.6rem);
    line-height: getpc(3.2rem);
    font-weight: $medium;
    @include desktop {
      font-size: 1.6rem;
      line-height: 3.2rem;
    }
  }

  &__answer-icon {
    position: relative;
    height: getpc(34px);
    min-width: getpc(34px);
    background: url('../img/icons/a.png');
    background-size: getpc(34px) getpc(34px);
    height: getpc(34px);
    min-width: getpc(34px);
    @include desktop {
      min-width: 34px;
      height: 34px;
      background-size: 34px 34px;
    }

    &::after {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      content: 'A';
      font-family: $novel;
      font-size: getpc(2rem);
      color: $white;
      @include center;
      height: 100%;
      width: 100%;
      letter-spacing: -1px;
      @include desktop {
        font-size: 2rem;
      }
    }
  }

  &__contact {
    &--confirm {
      #{$p}__form-privacy {
        display: none;
      }
    }
  }

  &__contact-title {
    font-size: getpc(2.8rem);
    font-weight: $bold;
    text-align: center;
    @include desktop {
      font-size: 2.8rem;
    }
    @include sp {
      font-size: getsp375(3.2rem);
      border-bottom: 1px solid $blue;
      margin: getsp375(30px) 0;
      padding-bottom: getsp375(30px);
    }
  }

  &__contact-information {
    display: flex;
    justify-content: space-between;
    margin-top: getpc(25px);
    @include desktop {
      margin-top: 25px
    }
    @include sp {
      @include center;
      flex-wrap: wrap;
      margin-top: 0;
      gap: getsp375(23px);
    }
  }
  
  &__contact-phoneicon {
    height: getpc(56px);
    width: getpc(56px);
    align-self: center;
    @include desktop {
      width: 56px;
      height: 56px;
    }
    @include sp {
      height: getsp375(65.14px);
      width: getsp375(65.14px);
    }
  }

  &__contact-number {
    font-family: $novel;
    font-size: getpc(4rem);
    color: $blue;
    font-weight: $bold;
    @include desktop {
      font-size: 4rem;
    }
    @include sp {
      font-size: getsp375(5rem);
    }
  }

  &__contact-schedule {
    font-size: getpc(1.6rem);
    align-self: center;
    @include desktop {
      font-size: 1.6rem;
    }
    @include sp {
      font-size: getsp375(2.4rem);
    }
  }
}
