.lp {
  $p: &;

  // COMMON
  &__wrapper {
    width: 100%;
  }

  &__section {
    background-color: $white;
    @include sp {
      overflow: hidden;
    }

    &--gray {
      background-color: $white2;
    }

    &--worry {
      position: relative;
      background: url("../img/worry-bg.svg") no-repeat center / cover;
      @include pc-lg {
        background-position-y: -65px;
      }
      @include sp {
        background-position-y: -20px;
      }
    }
  }
  
  &__section-wrap {
    flex-direction: column;
    padding: getpc(80px) 0;
    @include max-width;
    @include center;
    @include desktop {
      padding: 80px 0;
    }
    @include sp {
      padding: 30px $sp-pad;
    }

    &--worry {
      padding-bottom: 0;
    }
  }

  &__heading-wrap {
    position: relative;
    width: 85%;
    margin: 60px auto 40px;
    @include center;
    @include sp {
      width: 100%;
      margin: getsp375(60px) auto getsp375(40px);
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, 50%);
      width: 100%;
      height: 1px;
      background-color: $blue;
    }
  }

  &__heading {
    position: relative;
    margin: 0 auto getpc(60px);
    padding-bottom: 30px;
    font-size: getpc(4.8rem);
    font-weight: $bold;
    line-height: getem(48, 26);
    letter-spacing: 0.05em;
    @include desktop {
      margin: 0 auto 60px;
      font-size: 4.8rem;
    }
    @include sp {
      width: 90%;
      margin: 0 0 30px;
      padding-bottom: 10px;
      font-size: 2.1rem;
      line-height: getem(24, 32);
      text-align: center;
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
      width: getpc(91px);
      height: getpc(7px);
      background-color: $primary;
      border-radius: 7px;
      @include desktop {
        width: 91px;
        height: 7px;
      }
      @include sp {
        left: 0;
        z-index: 1;
        transform: none;
        width: 42px;
        height: 4px;
        border-radius: 4px;
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
      }
    }

    &::after {
      @include sp {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 4px;
        background-color: $gray;
        border-radius: 4px;
      }
    }

    &--worry {
      margin-bottom: getpc(40px);
      @include desktop {
        margin-bottom: 40px;
      }
      @include sp {
        width: 100%;
        margin-bottom: 30px;
        font-size: 1.9rem;
      }
    }

    &--small {
      position: relative;
      text-align: center;
      font-size: getpc(3.6rem);
      line-height: getpc(3.6rem);
      margin: getpc(60px) 0 getpc(40px);
      padding: 0 getpc(24px);
      z-index: 1;
      width: 100%;
      max-width: getpc(1000px);
      @include desktop {
        font-size: 3.6rem;
        line-height: 3.6rem;
        margin: 60px 0 40px;
        padding: 0 24px;
      }
      @include sp {
        padding: 0 getsp375(20px);
        font-size: getsp375(3.6rem);
        line-height: getem(36, 26);
      }
      
      &::before {
        content: '';
        position: absolute;
        z-index: -1;
        left: 0;
        bottom: 50%;
        width: 100%;
        height: 1px;
        background-color: $blue;
      }
    }

    &--small {
      &::after {
        background-color: $white2;
        content: attr(data-name);
        height: 100%;
        width: max-content;
        top: 0;
        left: 0; 
        right: 0; 
        margin-left: auto; 
        margin-right: auto; 
        position: absolute;
        padding: 0 getsp375(23px);
        @include sp {
          padding: 0 getsp375(13.75px);
        }
      }
    }

    &--consult {
      background-color: $white;
      text-align: center;
      font-size: getpc(3.6rem);
      line-height: getpc(3.6rem);
      margin: 0;
      padding: 0 getpc(24px);
      z-index: 1;
      width: auto;
      @include center;
      @include desktop {
        font-size: 3.6rem;
        line-height: 3.6rem;
        padding: 0 24px;
      }
      @include sp {
        padding: 0 getsp375(20px);
        font-size: getsp375(3.6rem);
        line-height: getem(36, 26);
      }

      &::before,
      &::after {
        content: none;
      }

      &.gray {
        background-color: $white2;
      }
    }
  }

  &__block {
    display: flex;
    flex-direction: column;
    width: 100%;

    &--worry {
      &:not(:last-child) {
        margin-bottom: getpc(100px);
        @include sp {
          margin-bottom: getsp375(100px);
        }
      }
    }

    &--border-green {
      border: getpc(7px) solid $green;
      background-color: $white2;
      border-radius: getpc(30px);
      min-height: getpc(357px);
      height: auto;
      width: 100%;
      padding: 0 10%; // 10% = 124/1200
      position: relative;
      display: flex;
      flex-direction: row;
      filter: drop-shadow(0 getpc(10px) getpc(10px) rgba($black, 0.15));
      @include desktop {
        border: 7px solid $green;
        border-radius: 30px;
        min-height: 357px;
      }
      @include sp {
        padding: getsp375(45px) $sp-pad getsp375(65px) $sp-pad;
      }
    }

    &--border-green #{$p}__button {
      width: getpc(368px);
      height: getpc(60px);
      padding: getpc(20px);
      background-color: $orange;
      border-radius: getpc(15px);
      transition: 0.2s ease;
      @include center;
      position: relative;
      @include desktop {
        width: 368px;
        height: 60px;
        padding: 20px;
        border-radius: 15px;
      }
      @include sp {
        width: max-content;
        height: getsp375(65px);
        padding: getsp375(50px);
        margin: 50px auto 0;
      }
  
      span {
        position: relative;
        padding-right: getpc(50px);
        color: $white;
        font-size: getpc(2.1rem);
        font-weight: $bold;
        letter-spacing: 0.1em;
        @include desktop {
          padding-right: 50px;
          font-size: 2.1rem;
        }
        @include sp {
          padding-right: 36px;
          font-size: getsp375(3.6rem);
        }
      }
  
      &::after {
        z-index: 1;
        content: '';
        position: absolute;
        top: 50%;
        right: -80px;
        transform: translateY(-50%);
        height: getpc(76px);
        width: getpc(91.7px);
        background: url("../img/speech-balloon.png") no-repeat center;
        background-size: 100%;
        @include desktop {
          right: -110px;
          height: 76px;
          width: 91.7px;
        }
        @include sp {
          background: url("../img/speech-balloon-long.png") no-repeat center;
          height: getsp375(90px);
          width: getsp375(240px);
          background-size: cover;
          right: -10px;
          top: -15px;
        }
      }
  
      &-wrap {
        margin: getpc(25px) 0 getpc(50px);
        display: flex;
        justify-content: right;
        margin-right: getpc(120px);
        @include desktop {
          margin: 25px 0 50px;
          display: flex;
          justify-content: right;
          margin-right: 120px;
        }
        @include sp {
          margin: 0;
        }
      }
    }

    &--small {
      margin-top: getpc(37px);
      margin-left: getpc(65px);
      height: max-content; 
      width: getpc(603px);
      min-width: getpc(490px);
      @include desktop {
        margin-top: 37px;
        margin-left: 65px;
        width: 603px;
        min-width: 490px;
      }
      @include sp {
        width: 100%;
        margin: 0;
        min-width: 100%;
      }
    }

    &--title {
      margin-top: getpc(5px);
      font-size: getpc(2.4rem);
      line-height: getpc(2.4rem);
      letter-spacing: 1px;
      @include desktop {
        margin-top: 5px;
        font-size: 2.4rem;
        line-height: 2.4rem;
      }
      @include sp {
        text-align: center;
        font-size: getsp375(3rem);
        line-height: getsp375(3rem);
      }
    }

    &--subtitle {
      margin-top: getpc(20px);
      font-size: getpc(3.6rem);
      line-height: getpc(3.6rem);
      color: $orange;
      margin-bottom: getpc(20px);
      letter-spacing: getpc(2px);
      @include desktop {
        margin-top: 20px;
        font-size: 3.6rem;
        line-height: 3.6rem;
        margin-bottom: 20px;
        letter-spacing: 2px;
      }
      @include sp {
        text-align: center;
        font-size: getsp375(4.6rem);
        line-height: getsp375(4.6rem);
      }
    }

    &--contact {
      margin-top: getpc(100px);
      max-width: getpc(1000px);
      padding: getpc(40px) getpc(185px);
      background-color: $white;
      border-radius: getpc(30px);
      box-shadow: 0 10px 10px rgba($black, 0.1);
      @include desktop {
        max-width: 1000px;
        margin-top: 100px;
        padding: 40px 185px;
        border-radius: 30px;
      }
      @include sp {
        margin-top: getsp375(91px);
        max-width: getsp375(630px);
        padding: getsp375(40px) getsp375(63.5px);
      }
    }

    &.align-center {
      align-items: center;
    }
  }

  &__text {
    p {
      font-size: getpc(1.8rem);
      font-weight: $medium;
      line-height: getem(18, 36);
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 1.8rem;
      }
      @include sp {
        font-size: 1.4rem;
        line-height: getem(14, 25);
      }
    }

    span {
      &.note {
        font-size: getpc(1.4rem);
        font-weight: $medium;
        line-height: getem(16, 32);
        @include desktop {
          font-size: 1.4rem;
        }
        @include sp {
          font-size: getsp375(2.3rem);
          line-height: getem(12, 16);
        }
      }
    }

    &--point {
      @include sp {
        padding: 0 10px;
      }
    }
  }

  &__button-container {
    @include center;
    @include sp {
      flex-direction: column-reverse;
    }
  }

  &__button {
    width: getpc(368px);
    height: getpc(60px);
    padding: getpc(20px);
    background-color: $primary;
    border-radius: 15px;
    transition: 0.2s ease;
    @include center;
    @include desktop {
      width: 368px;
      height: 60px;
      padding: 20px;
    }
    @include sp {
      width: 100%;
    }

    span {
      position: relative;
      padding-right: 30px;
      color: $white;
      font-size: 2.1rem;
      font-weight: $bold;
      letter-spacing: 0.1em;

      &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 18px;
        height: 18px;
        background: url("../img/arrow-down.svg") no-repeat center;
        background-size: contain;
        transition: 0.2s ease;
      }
    }

    &:hover {
      opacity: 0.9;

      span {
        &::before {
          transform: translateY(-56%);
        }
      }
    }

    &--form {
      width: getpc(295px);
      height: getpc(60px);
      background-color: $blue;
      border-radius: getpc(15px);
      border: none;
      color: $white;
      font-size: getpc(2rem);
      font-weight: $bold;
      line-height: 1em;
      letter-spacing: 0.1em;
      outline: none;
      text-shadow: none;
      cursor: pointer;
      @include center;
      @include desktop {
        width: 294px;
        height: 60px;
        font-size: 2rem;
        border-radius: 15px;
      }
      @include sp {
        width: getsp375(589px);
        height: getsp375(96px);
        box-shadow: 0 10px 10px rgba($black, 0.4);
        font-size: getsp375(36px);
      }

      &.back {
        margin-right: getpc(20px);
        background-color: $gray2;
        @include desktop {
          margin-right: 20px;
        }
        @include sp {
          margin-top: getsp375(20px);
          margin-right: 0;
        }
      }
    }
  }
}
