.lp {
  &__footer {
    flex-direction: column;
    @include center;
  }

  &__footer-wrap {
    width: 100%;
    padding: getpc(60px) 0;
    background-color: $yellow;
    @include desktop {
      padding: 60px 0;
    }
    @include sp {
      padding: 30px $sp-pad 35px;
    }
  }

  &__footer-heading {
    position: relative;
    z-index: 1;
    width: 100%;
    margin-bottom: getpc(50px);
    @include center;
    @include desktop {
      margin-bottom: 50px;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: -1;
      transform: translate(-50%, 50%);
      width: 100%;
      height: 3px;
      background-color: $white2;
    }

    h2 {
      padding: 0 getpc(50px);
      background-color: $yellow;
      color: $white;
      font-size: getpc(4rem);
      font-weight: $bold;
      letter-spacing: 0.05em;
      @include desktop {
        padding: 0 50px;
        font-size: 4rem;
      }
      @include sp {
        padding: 0 getsp375(35px);
        font-size: 2rem;
      }
    }
  }

  &__footer-content {
    width: 100%;
    @include max-width;
    @include center;
    @include sp {
      flex-direction: column;
    }
  }

  &__footer-col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 50%;
    @include sp {
      width: 100%;
    }

    &:not(:last-child) {
      margin-right: getpc(60px);
      @include desktop {
        margin-right: 60px;
      }
      @include sp {
        margin-right: 0;
        margin-bottom: 25px;
      }
    }
  }

  &__footer-item {
    display: flex;
    align-items: flex-start;
    width: 100%;

    &:not(:last-child) {
      margin-bottom: getpc(45px);
      @include desktop {
        margin-bottom: 45px;
      }
      @include sp {
        margin-bottom: getps375(50px);
      }
    }
  }

  &__footer-label,
  &__footer-text {
    color: $white;
    font-size: getpc(2rem);
    font-weight: $bold;
    letter-spacing: 0.05em;
    @include desktop {
      font-size: 2rem;
    }
    @include sp {
      font-size: 1.3rem;
    }
    @include sp-sm {
      font-size: 1.1rem;
    }
  }

  &__footer-label {
    width: 25%;
    margin-right: getpc(40px);
    @include desktop {
      margin-right: 40px;
    }
    @include sp {
      margin-right: getsp375(30px);
      background-color: $white;
      border-radius: 7px;
      color: $yellow;
      text-align: center;
    }
  }

  &__footer-text {
    width: 75%;
  }

  &__footer-map {
    width: 100%;

    iframe {
      width: getpc(640px);
      height: getpc(340px);
      @include desktop {
        width: 640px;
        height: 340px;
      }
      @include sp {
        width: 100%;
        height: getsp375(357px);
      }
    }
  }

  &__footer-cr {
    padding: getpc(6px) 0;
    background-color: $white;
    @include desktop {
      padding: 6px 0
    }
    @include center;
    @include sp {
      padding: 5px 0;
    }
  }

  &__footer-cr-text {
    font-size: getpc(1.4rem);
    font-weight: $medium;
    @include desktop {
      font-size: 1.4rem;
    }
    @include sp {
      font-size: 0.9rem;
    }
  }
}
