@mixin center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin max-width {
  max-width: getpc(1200px);
  margin: 0 auto;
  box-sizing: border-box;
  @include desktop {
    max-width: 1200px;
  }
  @media screen and (max-width: 767px) {
    max-width: 100%;
    padding: 0 $sp-pad;
  }
}

@mixin ie11 {
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    @content;
  }
}

@mixin ios {
  @supports (-webkit-touch-callout: none) {
    @content;
  }
}

@mixin mac {
  @media not all and (min-resolution:.001dpcm) {
    @supports (-webkit-appearance:none) {
      @content;
    }
  }
}

@mixin mx1200 {
  @media screen and (max-width: 1200px) {
    @content;
  }
}

@mixin mx980 {
  @media screen and (max-width: 980px) {
    @content;
  }
}

@mixin desktop {
  @media screen and (max-width: 1920px) and (min-width: 1600px) {
    @content;
  }
}

@mixin pc-lg {
  @media screen and (max-width: 1920px) and (min-width: 1601px) {
    @content;
  }
}

@mixin pc-md {
  @media screen and (max-width: 1600px) and (min-width: 1441px) {
    @content;
  }
}

@mixin tablet {
  @media screen and (max-width: 1200px) and (min-width: 768px) {
    @content;
  }
}

@mixin sp-lg {
  @media screen and (max-width: 767px) and (min-width: 540px) {
    @content;
  }
}

@mixin sp-md {
  @media screen and (max-width: 539px) and (min-width: 425px) {
    @content;
  }
}

@mixin sp {
  @media screen and (max-width: 767px) {
    @content;
  }
}

@mixin sp-sm {
  @media screen and (max-width: 374px) and (min-width: 320px) {
    @content;
  }
}

@function getsp($val) {
  $result: calc(calc(330/750) * $val);
  // $result: (330/750) * $val;
  @return $result;
}

@function getsp375($val) {
  $result: calc(calc(375/750) * $val);
  // $result: (375/750) * $val;
  @return $result;
}

@function getem($base-size, $illust-lheight) {
  @return calc(calc($illust-lheight / $base-size) * 1em);
  // @return ($illust-lheight / $base-size) * 1em;
}

@function getpc($val) {
  // $result: calc(1366/1920) * $val;
  $result: calc(calc(1366/1920) * $val);
  @return $result;
}
