.white {
  color: $white;
}

.white-bg {
  background: $white;
}

.black {
  color: $black;
}

.black-bg {
  background: $black;
}

.primary {
  color: $primary;
}

.primary-sp {
  @include sp {
    color: $primary;
  }
}

.primary-bg {
  background: $primary;
}

.bold {
  font-weight: $bold;
}

.bold-sp {
  @include sp {
    font-weight: $bold;
  }
}

.overflow {
  overflow: hidden;
}

// image size
.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

// display
.pc {
  @include sp {
    display: none;
  }
}

.pc-flex {
  display: flex;
  @include sp {
    display: none !important;
  }
}

.sp {
  display: none;
  @include sp {
    display: block !important;
  }
}

.sp-flex {
  display: none;
  @include sp {
    display: flex !important;
  }
}
