.lp {
  $p: &;

  &__form {
    &--confirm {
      #{$p}__form-privacy {
        display: none;
      }

      #{$p}__form-label {
        padding-top: 0;
      }
    }
  }
  &__form-content {
    max-width: getpc(1000px);
    width: 100%;
    margin: auto;
    @include desktop {
      max-width: 1000px;
    }
  }

  &__form-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @include sp {
      flex-direction: column;
    }

    &:not(:last-child) {
      margin-bottom: getpc(40px);
      @include desktop {
        margin-bottom: 40px;
      }
      @include sp {
        margin-bottom: getsp375(30px);
      }
    }
  }

  &__form-label {
    display: flex;
    align-items: center;
    width: 30%;
    padding-top: getpc(15px);
    font-size: getpc(2rem);      
    font-weight: $bold;
    letter-spacing: 0.05em;
    @include desktop {
      padding-top: 15px;
      font-size: 2rem;
    }
    @include sp {
      width: 100%;
      margin-bottom: getsp375(20px);
      font-size: 1.3rem;
    }

    .required {
      margin-left: getpc(20px);
      background-color: $primary;
      color: $white;
      border-radius: 5px;
      height: getpc(21px);
      font-size: getpc(1.6rem);
      line-height: 1em;
      width: getpc(49px);
      padding-top: 2px;
      @include center;
      @include desktop {
        width: 49px;
        height: 21px;
        margin-left: 20px;
        font-size: 1.6rem;
      }
      @include sp {
        height: getsp375(26px);
        margin-left: getsp375(26px);
        font-size: getsp375(2rem);
        width: getsp375(60px);
        padding-top: 1px;
      }
    }
  }

  &__form-input-wrap {
    width: 70%;
    @include sp {
      width: 100%;
    }
  }

  &__form-input {
    input[type=text],
    textarea {
      line-height: getem(20, 35);
      border: 1px solid $blue;
      border-radius: getpc(15px);
      // max-width: getpc(733px);
      width: 100%;
      padding: getpc(20px) getpc(42px);
      font-size: getpc(2rem);      
      font-weight: $bold;
      letter-spacing: 0.05em;
      outline: none;
      @include desktop {
        padding: 20px 42px;
        border-radius: 15px;
        font-size: 2rem;
      }
      @include sp {
        max-width: 100%;
        font-size: 1.6rem;
        line-height: getem(26, 42);
        padding: getsp375(22px) getsp375(30px);
      }

      &::placeholder {
        color: #CDCFD1;
        font-size: getpc(2rem);      
        font-weight: $bold;
        letter-spacing: 0.05em;
        @include desktop {
          font-size: 2rem;
        }
        @include sp {
          font-size: 1.6rem;
        }
      }
    }

    input[type=text] {
      height: getpc(60px);
      @include desktop {
        height: 60px;
      }
      @include sp {
        height: getsp375(81px);
      }
    }

    textarea {
      height: getpc(340px);
      line-height: getem(20, 35);
      resize: none;
      @include desktop {
        height: 340px;
      }
      @include sp {
        height: getsp375(240px);
      }
    }

    p {
      width: 100%;
      font-size: getpc(2rem);      
      font-weight: $bold;
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 2rem;
      }
      @include sp {
        max-width: 100%;
        font-size: 1.3rem;
        line-height: getem(26, 42);
        padding: getsp375(22px) getsp375(30px);
      }
    }
  }

  &__form-privacy {
    margin: getpc(50px) auto;
    width: max-content;
    display: flex;
    align-items: center;
    height: max-content;
    position: relative;
    padding-left: getpc(55px);
    @include desktop {
      margin: 50px auto;
      padding-left: 55px;
    }
    @include sp {
      margin: getsp375(50px) auto;
      padding-left: getsp375(60px);
      height: getsp375(38px);
    }
  }

  &__form-checkbox {
    input[type=checkbox] {
      outline: none;
      box-shadow: none;
      appearance: none;
      position: absolute;
      top: 0;
      left: 0;
      cursor: pointer;
  
      &::before{
        content: '';
        width: getpc(30px);
        height: getpc(30px);
        border: 1px solid $blue;
        background-color: $white;
        outline: getpc(3px) solid $white;
        outline-offset: getpc(-5px);
        border-radius: getpc(10px);
        position: absolute;
        top: 0;
        left: 0;
        @include desktop {
          width: 30px;
          height: 30px;
        }
        @include sp {
          width: getsp375(38px);
          height: getsp375(38px);
          border-radius: getsp375(11.35px);
        }
      }
  
      &::after {
        content: '';
        width: 5px;
        height: 10px;
        border: solid $blue;
        border-width: 0 3px 3px 0;
        position: absolute;
        left: 8px;
        top: 4px;
        transform: rotate(45deg) scale(0);
        opacity: 0;
        visibility: hidden;
        transition: 0.2s ease;
        @include sp {
          left: 7px;
          top: 2px;
          transform: rotate(45deg);
        }
      }
  
      &:checked {
        &::after {
          opacity: 1;
          visibility: visible;
          transform: rotate(45deg) scale(1);
        }
      }
    }

    span {
      font-size: getpc(2rem);      
      font-weight: $bold;
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 2rem;
      }

      &.sp-link {
        @include sp {
          color: $blue;
          text-decoration: underline;
          text-underline-offset: 4.34px;
        }
      }
    }
  }
}
