.lp {
  $p: &;

  &__head {
    position: relative;
    width: 100%;
    height: getpc(716px);
    padding-top: getpc(40px);
    overflow: hidden;
    @include desktop {
      height: 716px;
      padding-top: 40px;
    }
    @include sp {
      height: auto;
    }
  
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: -22%;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: url("../img/lp-bg.svg") no-repeat center;
      background-size: cover;
      @include desktop {
        left: -20%;
      }
      @include tablet {
        left: -25%;
        width: 108%;
      }
      @include sp {
        left: unset;
        background-image: url("../img/lp-bg-sp.svg");
      }
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -2;
      width: 100%;
      height: 100%;
      background-color: $white2;
    }
  }

  &__head-logo {
    width: 150px;
    height: 36px;
    padding-left: 40px;
  }

  &__head-content {
    position: relative;
    display: flex;
    overflow: hidden;
    @include sp {
      flex-direction: column;
    }
  }

  &__head-col {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 60%;
    @include sp {
      width: 100%;
    }

    &--circle {
      position: absolute;
      right: -40px;
      align-items: center;
      @include pc-lg {
        right: -80px;
      }
      @include pc-md {
        right: -100px;
      }
      @include tablet {
        right: -80px;
      }
      @include sp {
        position: relative;
        right: unset;
        flex-direction: column-reverse;
      }
      @include sp-lg {
        padding-bottom: 20px;
      }
    }
  }

  &__head-title-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    margin-top: 36px;
    @include desktop {
      margin-top: 50px;
    }
  }

  &__head-title {
    position: relative;
    display: flex;
    align-items: baseline;
    width: auto;
    height: getpc(122px);
    padding-left: getpc(200px);
    padding-right: getpc(60px);
    font-size: getpc(6rem);
    font-weight: $bold;
    letter-spacing: 0.075em;
    @include desktop {
      height: 122px;
      padding-left: 200px;
      padding-right: 60px;
      font-size: 6rem;
    }
    @include pc-md {
      padding-left: 120px;
    }
    @include tablet {
      padding-left: getpc(100px);
      font-size: getpc(5.4rem);
    }
    @include sp {
      height: getsp375(105px);
      padding-left: getsp375(65px);
      font-size: 2.5rem;
    }
    @include sp-lg {
      height: getsp375(120px);
      padding-left: 60px;
      font-size: 3rem;
    }
    @include sp-sm {
      font-size: 2rem;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background-color: $white;
      border-top-right-radius: 25px;
      border-bottom-right-radius: 25px;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
      @include sp {
        margin-bottom: 10px;
      }
    }

    span {
      position: relative;
      &.highlight {
        color: $primary;
        font-size: getpc(8rem);
        @include desktop {
          font-size: 8rem;
        }
        @include tablet {
          font-size: getpc(7rem);
        }
        @include sp {
          font-size: getsp375(6.8rem);
        }
        @include sp-lg {
          font-size: 4rem;
        }
        @include sp-sm {
          font-size: 3rem;
        }

        &::before {
          content: '';
          position: absolute;
          top: -15px;
          right: -15px;
          width: getpc(63px);
          height: getpc(46px);
          background: url("../img/title-decor.svg") no-repeat center;
          @include desktop {
            width: 63px;
            height: 46px;
          }
          @include sp {
            top: -10px;
            right: -10px;
            width: getsp375(55px);
            height: 20px;
          }
        }
      }
    }

    &.align-center {
      align-items: center;
    }
  }

  &__head-text {
    padding-top: getpc(60px);
    padding-left: getpc(200px);
    @include desktop {
      padding-top: 60px;
      padding-left: 200px;
    }
    @include pc-md {
      padding-left: 120px;
    }
    @include tablet {
      padding-left: getpc(100px);
    }
    @include sp {
      padding-top: 30px;
      padding-left: getsp375(65px);
    }

    p {
      color: $white;
      font-size: getpc(2.8rem);
      font-weight: $bold;
      line-height: getem(28, 56);
      letter-spacing: 0.05em;
      text-shadow: 0 2px 10px rgba($brown, 0.3);
      @include desktop {
        font-size: 2.8rem;
      }
      @include sp {
        font-size: 1.5rem;
      }
      @include sp-lg {
        font-size: 1.8rem;
      }
      @include sp-sm {
        font-size: 1.4rem;
      }
    }
  }

  &__head-circle {
    padding-bottom: getpc(20px);
    @include center;
    @include desktop {
      padding-bottom: 20px;
    }
    @include sp {
      z-index: 1;
      margin-top: -100px;
      padding-bottom: 0;
    }

    &--solve {
      margin: getpc(30px) auto 0;
      @include desktop {
        margin-top: 30px;
      }
      @include sp {
        margin-top: getsp375(23px);
      }
    }
  }

  &__head-circle-item {
    width: getpc(168px);
    height: getpc(168px);
    background: $primary;
    border-radius: 50%;
    color: $white;
    font-size: getpc(2.4rem);
    font-weight: $bold;
    line-height: getem(24, 43);
    text-align: center;
    @include center;
    @include desktop {
      width: 168px;
      height: 168px;
      font-size: 2.4rem;
    }
    @include tablet {
      width: getpc(160px);
      height: getpc(160px);
    }
    @include sp {
      width: getsp375(191px);
      height: getsp375(191px);
      font-size: 1.4rem;
    }

    &:not(:last-child) {
      margin-right: getpc(50px);
      @include sp {
        margin-right: 15px;
      }
    }

    span.num {
      font-size: getpc(3.8rem);
      @include sp {
        font-size: 2.2rem;
      }
    }
  }

  &__head-img {
    width: 50%;
    @include sp {
      width: 70%;
      max-width: getsp375(400px);
      margin-top: 30px;
    }
    @include sp-lg {
      max-width: 300px;
    }
  }

  &__worry {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }

  &__worry-text-wrap {
    position: relative;
    z-index: 1;
    width: 90%;
    padding: getpc(80px) 20px 140px;
    background: url("../img/worry-bg2.svg") no-repeat top / cover;
    border-radius: 20px;
    @include desktop {
      width: 80%;
      padding: 80px 20px 170px;
    }
    @include sp {
      width: 85%;
      padding: getsp375(80px) 0 70px;
      background-image: url("../img/worry-bg2-sp.svg");
    }
    @include sp-lg {
      width: 75%;
      padding-bottom: 84px;
      background-image: url("../img/worry-bg2.svg");
    }
    @include sp-md {
      background-image: url("../img/worry-bg2.svg");
    }
  }

  &__worry-text {
    width: 80%;
    margin: 0 auto;
    @include sp {
      width: 90%;
    }
    @include sp-lg {
      width: 80%;
    }

  }

  &__worry-img {
    width: 90%;
    margin: getpc(30px) auto;
  }

  &__worry-list {
    display: flex;
    flex-direction: column;
  }

  &__worry-item {
    position: relative;
    padding: 6px getpc(36px) 6px getpc(60px);
    background-color: $white2;
    border-radius: 18.5px;
    color: $blue;
    font-size: getpc(1.8em);
    font-weight: $bold !important;
    @include desktop {
      font-size: 1.8rem;
    }
    @include sp {
      padding: 6px 10px 6px getsp375(60px);
      font-size: 1.4rem;
    }

    &::before {
      content: '';
      position: absolute;
      top: 11px;
      left: 20px;
      width: getpc(20px);
      height: getpc(20px);
      background: url("../img/worry-marker.svg") no-repeat center;
      @include desktop {
        width: 20px;
        height: 20px;
      }
      @include sp {
        top: 9px;
        left: 12px;
      }
    }

    &:not(:last-child) {
      margin-bottom: 15px;
      @include sp {
        margin-bottom: 10px;
      }
    }
  }

  &__solve {
    z-index: 1;
    display: flex;
    flex-direction: column;
    margin-top: -20px;
  }

  &__solve-bubble {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: baseline;
    justify-content: center;
    padding: 50px 0;
    font-size: getpc(6rem);
    @include desktop {
      margin-top: 10px;
      font-size: 6rem;
    }
    @include sp {
      display: none;
    }

    &::before {
      content: '';
      position: absolute;
      top: 20px;
      left: 50%;
      transform: translateX(-50%);
      z-index: -1;
      width: 110%;
      height: 100%;
      background: url("../img/worry-bubble.svg") no-repeat center;
      background-size: 100%;
      @include sp {
        content: none;
      }
    }

    span.highlight {
      color: $primary;
      font-size:getpc(8rem);
      @include desktop {
        font-size: 8rem;
      }
    }
  }

  &__solve-block {
    position: relative;
    display: flex;
    align-items: center;
    justify-self: flex-start;
    @include sp {
      flex-direction: column-reverse;
    }
  }

  &__solve-list {
    z-index: 1;
    display: flex;
    flex-wrap: wrap;
    width: getpc(455px);
    margin-top: 20px;
    margin-left: 60px;
    border-radius: 25px;
    overflow: hidden;
    @include desktop {
      width: 455px;
      margin-left: 5%;
    }
    @include tablet {
      margin-left: 50px;
    }
    @include sp {
      width: 100%;
      margin-top: 10px;
      margin-left: unset;
      border-radius: unset;
      overflow: unset;
    }
  }

  &__solve-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: calc(getpc(455px) / 2);
    padding: getpc(40px);
    background-color: $white2;
    @include desktop {
      width: calc(455px / 2);
      padding: 30px;
    }
    @include sp {
      width: calc(50% - 8px);
      padding: 10px 20px;
      border-radius: 20px;
    }

    &:first-child,
    &:nth-child(3) {
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        width: 2px;
        height: 80%;
        background-color: $secondary;
        @include sp {
          content: none;
        }
      }
    }

    &:nth-child(3),
    &:nth-child(4) {
      @include sp {
        margin-top: 15px;
      }

      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 15px;
        width: 100%;
        height: 2px;
        background-color: $secondary;
        @include sp {
          content: none;
        }
      }
    }

    &:nth-child(4) {
      &::after {
        left: 0;
        width: 90%;
      }
    }

    &:not(:nth-child(2n)) {
      @include sp {
        margin-right: 15px;
      }
    }
  }

  &__solve-num {
    position: relative;
    width: getpc(70px);
    height: getpc(70px);
    padding-top: getpc(20px);
    background-color: $blue;
    border-radius: 8px;
    color: $white;
    font-family: $novel;
    font-size: getpc(3.5rem);
    text-align: center;
    @include center;
    @include desktop {
      width: 70px;
      height: 70px;
      padding-top: 20px;
      font-size: 3.5rem;
    }
    @include sp {
      width: getsp375(70px);
      height: getsp375(70px);
      padding-top: 10px;
      font-size: getsp375(3.8rem);
    }

    &::before {
      content: 'POINT';
      position: absolute;
      top: 8px;
      left: 50%;
      transform: translateX(-50%);
      width: 100%;
      height: 100%;
      color: $white;
      font-family: $novel;
      font-size: getpc(1.3rem);
      text-align: center;
      @include desktop {
        font-size: 1.3rem;
      }
      @include sp {
        top: 3px;
        font-size: 0.8rem;

      }
    }
  }

  &__solve-text {
    margin-top: 10px;
    font-size: getpc(2rem);
    font-weight: $bold;
    text-align: center;
    letter-spacing: 0.05em;
    @include desktop {
      font-size: 2rem;
    }
    @include sp {
      font-size: 1.6rem;
      line-height: getme(16, 22);
    }
  }

  &__solve-img {
    position: absolute;
    right: -110px;
    width: 80%;
    @include tablet {
      right: -80px;
    }
    @include sp {
      position: relative;
      right: unset;
      width: 150%;
      margin-top: 20px;
    }
    @include sp-lg {
      width: 100%;
    }
  }

  &__points {
    flex-direction: column;
    width: 90%;
    @include center;
    @include sp {
      width: 100%;
    }
  }

  &__point-item {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: getpc(40px) getpc(60px);
    border-radius: 30px;
    box-shadow: 0 10px 10px rgba($dark-blue, 0.15);
    @include desktop {
      padding: 40px 60px;
    }
    @include sp {
      padding: 15px;
    }

    &:not(:last-child) {
      margin-bottom: getpc(35px);
      @include sp {
        margin-bottom: getsp375(60px);
      }
    }
  }

  &__point-head {
    position: relative;
    display: flex;
    align-items: center;
    padding-bottom: getpc(30px);
    @include desktop {
      padding-bottom: 30px;
    }
    @include sp {
      margin-bottom: getsp375(35px);
      padding-bottom: getsp375(40px);
    }

    &::before,
    &::after {
      @include sp {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: getsp375(85px);
        height: 3px;
        background-color: $primary;
      }
    }

    &::before {
      @include sp {
        width: 100%;
        background-color: $gray;
      }
    }
  }

  &__point-title {
    padding-left: getpc(25px);
    font-size: getpc(3rem);
    font-weight: $bold;
    @include desktop {
      padding-left: 25px;
      font-size: 3rem;
    }
    @include sp {
      width: calc(100% - 35px);
      padding-left: getsp375(25px);
      font-size: getsp375(3.8rem);
      line-height: getem(19, 28);
    }
  }

  &__point-info {
    display: flex;
    flex-direction: column;
    margin-top: getpc(60px);
    padding-top: getpc(60px);
    border-top: 1px solid $primary;
    @include desktop {
      margin-top: 60px;
      padding-top: 60px;
    }
    @include sp {
      margin-top: 30px;
      padding: 30px 10px 0;
    }
  }

  &__point-info-row {
    display: flex;
    align-items: center;
  }

  &__point-heading {
    position: relative;
    z-index: 1;
    margin: 0 auto getpc(20px);
    font-size: getpc(2.4rem);
    font-weight: $bold;
    text-align: center;
    letter-spacing: 0.05em;
    @include center;
    @include desktop {
      margin: 0 auto 20px;
      font-size: 2.4rem;
    }
    @include sp {
      font-size: 1.7rem;
    }

    &::before {
      content: '';
      position: absolute;
      left: -3px;
      bottom: 5px;
      z-index: -1;
      width: 85%;
      height: 9px;
      background-color: $secondary;
    }
  }

  &__point-img {
    width: 40%;
    margin-right: getpc(50px);
    @include desktop {
      margin-right: 50px;
    }
  }

  &__point-text {
    p {
      font-size: getpc(1.6rem);
      font-weight: $medium;
      line-height: getem(16, 32);
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 1.6rem;
      }
      @include sp {
        font-size: 1.4rem;
        line-height: getem(14, 25);
      }
    }
  }

  &__point-table-wrap {
    width: 100%;
    margin: getpc(50px) auto getpc(30px);
    @include desktop {
      margin: 50px auto 30px;
    }
  }

  &__point-table {
    display: table;
    border-collapse: collapse;
    width: 100%;
    
    tr {
      border-bottom: 1px solid $dark-blue;
      @include sp {
        border-bottom-color: $gray2;
      }

      &:first-child {
        border-top: 1px solid $dark-blue;
        @include sp {
          border-top-color: $gray2;
        }
      }
    }

    th,
    td {
      padding: 6px 20px;
      font-size: getpc(1.6rem);
      font-weight: $medium;
      @include desktop {
        padding: 10px 30px;
        font-size: 1.6rem;
      }
      @include sp {
        padding: 6px 15px;
        font-size: getsp375(2.5rem);
      }
    }

    th {
      width: 35%;
      background-color: $blue;
      color: $white;
      font-weight: $bold;
      text-align: center;

      &:first-child {
        width: 30%;
        border-right: 1px solid $dark-blue;
        @include sp {
          border-left: 1px solid $gray2;
        }
      }

      &:not(:last-child) {
        @include sp {
          border-right: 1px solid $white;
        }
      }

      &:last-child {
        @include sp {
          border-right-color: $gray2;
        }
      }

    }

    td {
      @include sp {
        border-right: 1px solid $gray2 !important;
      }

      &:first-child {
        border-right: 1px solid $dark-blue;
        text-align: center;
        @include sp {
          border-left: 1px solid $gray2;
        }
      }

      &.bold {
        font-weight: $bold;
      }

      &.sp-center {
        text-align: center;
      }
    }
  }

  &__achieve {
    flex-wrap: wrap;
    margin-bottom: getpc(50px);
    @include center;
    @include desktop {
      margin-bottom: 50px;
    }
    @include sp {
      display: block;
      flex-wrap: unset;
      width: 100%;
      margin-bottom: 50px;
    }
  }

  &__achieve-item {
    flex-direction: column;
    width: 30%;
    @include center;
    @include sp {
      width: 100%;
      padding: 20px 30px 0;
    }

    &:not(:last-child) {
      margin-right: getpc(60px);
      @include desktop {
        margin-right: 60px;
      }
      @include sp {
        margin-right: 0;
      }
    }

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:nth-last-child(-n+2) {
      margin-top: getpc(60px);
      @include desktop {
        margin-top: 60px;
      }
      @include sp {
        margin-top: unset;
      }
    }
  }

  &__achieve-text {
    width: 100% ;
    margin-top: getpc(20px);
    padding: 4px getpc(35px);
    background-color: $blue;
    border-radius: 16px;
    color: $white;
    font-size: getpc(1.8rem);
    text-align: center;
    @include desktop {
      margin-top: 20px;
      padding: 4px 35px;
      font-size: 1.8rem;
    }
    @include sp {
      padding: 4px 10px;
      font-size: 1.4rem;
    }
  }

  &__perform {
    width: 90%;
    margin: 0 auto;
    @include sp {
      width: 100%;
    }
  }

  &__perform-item {
    display: flex;
    align-items: center;
    @include sp {
      flex-direction: column-reverse;
      align-items: flex-start;
      justify-content: flex-start;
    }

    &:not(:last-child) {
      margin-bottom: getpc(60px);
      @include desktop {
        margin-bottom: 60px;
      }
      @include sp {
        margin-bottom: getsp375(70px);
      }
    }
  }

  &__perform-img {
    width: 30%;
    margin-right: getpc(60px);
    overflow: hidden;
    @include desktop {
      margin-right: 60px;
    }
    @include sp {
      width: 100%;
      max-height: 50vw;
      margin-top: 20px;
      margin-right: 0;
    }
  }

  &__perform-text {
    width: 70%;
    @include sp {
      width: 100%;
    }

    p {
      font-size: getpc(1.6rem);
      font-weight: $medium;
      line-height: getem(16, 32);
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 1.6rem;
      }
      @include sp {
        font-size: 1.3rem;
      }
    }
  }

  &__media {
    width: 105%;
    margin-bottom: getpc(40px);
    @include center;
    @include desktop {
      margin-bottom: 40px;
    }
    @include sp {
      flex-wrap: wrap;
      width: 100%;
    }
  }

  &__media-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 20px 30px;
    border-radius: 30px;
    box-shadow: 0 10px 10px rgba($black, 0.2);
    @include sp {
      padding: 9px 10px 12px;
    }

    &:not(:last-child) {
      width: 20%;
      margin-right: getpc(60px);
      @include desktop {
        margin-right: 60px;
      }
      @include sp {
        width: calc(50% - 10px);
        margin-right: 20px;
      }
    }

    &:nth-child(2) {
      @include sp {
        margin-right: 0;
      }
    }

    &:last-child {
      width: 60%;
      @include sp {
        width: 100%;
        margin-top: 20px;
      }
    }
  }

  &__media-title {
    width: 100%;
    margin-bottom: getpc(14px);
    padding-bottom: getpc(14px);
    border-bottom: 1px solid $blue;
    font-size: getpc(2.2rem);
    font-weight: $bold;
    text-align: center;
    letter-spacing: 0.05em;
    @include desktop {
      margin-bottom: 14px;
      padding-bottom: 14px;
      font-size: 2.2rem;
    }
    @include sp {
      margin-bottom: 10px;
      padding-bottom: 5px;
      font-size: getsp375(3.1rem);
    }
  }

  &__media-item {
    flex-direction: column;
    @include center;
    @include sp {
      width: 50%;
    }

    &:not(:last-child) {
      margin-right: getpc(60px);
      @include desktop {
        margin-right: 60px;
      }
      @include sp {
        margin-right: 0
      }
    }

    &:not(:nth-last-child(-n+2)) {
      @include sp {
        margin-bottom: 15px;
      }
    }
  }

  &__media-item-group {
    width: 100%;
    @include center;
    @include sp {
      flex-wrap: wrap;
    }
  }

  &__media-text {
    @include center;

    span {
      color: $blue;
      font-size: getpc(1.8rem);
      font-weight: $bold;
      letter-spacing: 0.05em;
      @include desktop {
        font-size: 1.8rem;
      }
      @include sp {
        font-size: 1.3rem;
      }

      &:not(:last-child) {
        margin-right: getpc(20px);
        @include desktop {
          margin-right: 20px;
        }
        @include sp {
          margin-right: 10px;
        }
      }
    }
  }

  &__media-icon {
    height: getpc(77px);
    margin-top: getpc(15px);
    transition: 0.2s ease;
    @include desktop {
      height: 77px;
      margin-top: 15px;
    }
    @include sp {
      width: getsp375(117px);
      margin-top: 5px;
    }

    &:hover {
      opacity: 0.8;
      transform: translateY(-3px);
    }
  }
}
